.signUpForm {
  background-color: white;
  padding: 10px;
  border-radius: 8px;
  margin-top: 2vh;
  margin-bottom: 2vh;
}

.inline {
  display: inline;
  margin-right: 20px;
}

.form-toggle {
  margin-left: 50px;
}

.alert {
  width: 420px;
  margin: 0 Auto;
}

.signInForm {
  background-color: white;
  padding: 10px;
  border-radius: 8px;
  margin-top: 2vh;
  margin-bottom: 2vh;
}

.login-page-div {
  background: url("https://www.unlockunit.com/blog/wp-content/uploads/2018/02/sound-waves.jpg");
  /* background: url("./sound-wave.jpg"); */
  background-size: cover;
  height: 65vh;
  background-position: 50%;
  padding-top: 2vh;
  box-shadow: 0 5px 15px 5px lightslategrey;
  text-align: center;
}

.login-content {
  margin-top: 2vh;
  text-align: center;
}

.form-toggle {
  margin-right: 50px;
  padding: 15px 25px;
}

.btn-inline {
  display: inline;
  margin: 0 auto;
}

.title {
  font-weight: bold;
  text-align: left;
  margin-bottom: 0.25rem;
  margin-left: 5px;
}

.brand {
  font-size: 1.7em;
  margin-left: 15px;
}

.links {
  margin-left: 10px;
  font-size: 18px;
  text-decoration: none !important;
}

.nav {
  margin-right: 15px;
}

.current-user {
  color: white;
  display: inherit;
  margin-right: 20px;
}

.current-user p {
  margin: auto 0;
  margin-left: 15px;
}

.searchBar {
    text-align: center;
    margin: auto;
    width: 30%;
    margin-top: 2em;
}


p {
  margin-left: 25px;
}

.searchResults {
  width: 48%;
  float: left;
  border-style: solid;
  padding: 10px;

}

.savedResults {
  width: 48%;
  float: right;
  border-style: solid;
  padding: 10px;

}

.jumbotron {
  background: url(/static/media/green-sound-wave.069f76b0.jpg);
  background-size: cover;
  background-position: 50%;
  margin: 40px auto ;
  margin-bottom: 150px;
  margin-top: 0px;
  width: 100%;
  height: 25em;
  box-shadow : 0 0 200px #777777;
  border-radius: 0px;

}

.jumboTitle {
  text-align: center;
  font-size: 50px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  color: floralwhite;
}

.SearchBar {
  margin:  auto 15px;
  text-align: center;

}

